@charset "utf-8";



// Our variables
$base-font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;

$base-font-size:   20px;

$base-font-weight: 400;
$small-font-size:  $base-font-size * 0.875;
$base-line-height: 1.5;

$spacing-unit:     30px;

$text-color:       	black; //#fafafa;
$background-hr:		black; //#383838; // The two bars surrounding the title of the stop
$background-color: 	white; //#222222;
$brand-color:	   	#fdee00; //#ffff31; //#fdee00;
$background-darker: lighten($brand-color, 47%); //#1B1B1B;
$anchor-color:          darken($brand-color, 20%);
$anchor-hover-color:	darken($brand-color, 5%);

$player-now-playing-size:   13px;
$player-height:		64px;
$player-button-size:	48px;
$player-bgcolor:	white;
$player-scrubber-color:	darken($brand-color, 1%); // No idea
$player-loaded-color:	darken($brand-color, 20%); // Player's bar
$player-progress-color:		white; //lighten($brand-color, 10%); // Player's bar once read
$player-play-color:		lighten($brand-color, 5%); // No idea
$player-progress-height: 10px;

$tile-bgcolor:	   $brand-color;
$tile-color:	   #383838; //#fafafa;
$tile-size:		   56px;
$tile-size-large:  64px;
$tile-border-radius:	2px;

$grey-color:       $brand-color; // No idea
$grey-color-light: lighten($grey-color, 40%); // No idea
$grey-color-dark:  darken($grey-color, 25%); // No idea

// Width of the content area
$content-width:    800px;

$on-tiny:          310px;
$on-palm:          640px;
$on-laptop:        800px;

$height-600:       600px;

// Use media queries like this:
// @include media-query($on-palm) {
//     .wrapper {
//         padding-right: $spacing-unit / 2;
//         padding-left: $spacing-unit / 2;
//     }
// }
@mixin media-query($device) {
    @media screen and (max-width: $device) {
        @content;
    }
}

@mixin media-query-height($device) {
    @media screen and (min-height: $device) {
        @content;
    }
}


// Import partials from `sass_dir` (defaults to `_sass`)
@import
        "fonts",
        "base",
        "layout",
        "syntax-highlighting"
;